html,body{
	padding:0;
	margin: 0;
	height:100%;
	width:100%;
	background: #E1E8EC;

}


*{
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding:0;
  margin:0;
  text-decoration: none;
  list-style: none;
  font-family: 'Arimo', sans-serif;
}

header{
	width:100%;
	position: relative;
	border:5px solid #66757F;
	background-color:#55ACEE;
	border-radius: 5px; 
}

section{
	width:100%;
	position: relative;
	overflow: hidden;
}

#make-twyl{
	textarea{
	}	
}

.twyl-obj{
	width:100%;
	border:1px solid #66757F;
	background-color:#55ACEE;
	margin-bottom: 2%;
	border-radius: 5px;
	padding: 2%;
}

.flash-m{
	width: 100%;
	text-align:center;
	color:#55ACEE;
	-webkit-text-stroke:1px black;
}

.logo-bar{
	width:100%;
	padding:0 5%;
	position:relative;
	overflow: hidden;
}

.logo{
	float:left;
	.mouse{
	   float: left;
	   background: url(../images/mouse.png) no-repeat center center; 
	  -webkit-background-size: cover;
	  -moz-background-size: cover;
	  -o-background-size: cover;
	  background-size: cover;
	  width:100px;
	  height:100px;
	}
	.text{
		float:left;
		font-size: 10vmin;
		color:white;
		-webkit-text-stroke:2px black;
		font-weight: 700;

	}
}

.logio{
	float:right;
	a{
		font-size:6vmin;
		color: white;
		-webkit-text-stroke:1px black;
		 position: absolute;
  		top: 50%;
  		right: 5%;
  		transform: translateY(-50%);
	}

}

.user-p{
	width:25%;
	float:left;
	padding:2%;
	text-align: center;
	position:relative;
	.info{
		width:100%;
		padding:3%;
		height:200px;
		background:#66757F;
		border:2px solid #55ACEE;
		border-radius: 5px;
		color:#ffffff;
	}
}

.user-action{
	padding:2% 4%;
	width:75%;
	float:right;
}
